@import '../utils/mixins';
@import '../utils/variables';

/*SINGLELOCATION DETAILS PAGE*/

.singleLocation {
  height: 73px;
  width: 100%;
  left: 20px;
  top: 94px;
  @include MobileS {
    width: 100%;
    height: 53px;
  }
  @include Tab {
    height: 63px;
  }
  @include DesktopS {
    width: 100%;
    height: 77px;
    width: 100%;
    left: 100px;
    top: 573px;
  }
  /*TITLE SINGLELOCATION DETAILS PAGE*/
  &__title {
    width: 100%;

    @include Desktop {
      height: 51px;
      width: 573px;
      height: 77px;
      width: 80%;
      left: 100px;
      top: 573px;
    }
    @include DesktopXL {
      height: 115px;
    }
    /*TEXT SINGLELOCATION DETAILS PAGE*/
    &__text {
      font-size: 18px;
      margin-bottom: 10px;
      @include Desktop {
        font-size: 36px;
        margin-bottom: 0;
      }
      @include DesktopXL {
        font-size: 62px;
      }
    }
  }
  /*LOCATION (CITY) SINGLELOCATION DETAILS PAGE*/
  &__location {
    height: 22px;
    width: 100%;
    left: 20px;
    top: 415px;
    font-size: 14px;
    margin-top: 5px;
    @include Desktop {
      font-size: 18px;
      margin-top: 0;
    }
    @include DesktopXL {
      font-size: 36px;
      height: 40px;
    }
  }
  /*TAGS SINGLELOCATION DETAILS PAGE*/
  &__tags {
    width: 52%;
    height: 18px;
    left: 20px;
    top: 447px;
    margin-top: 2%;
    @include MobileL {
      width: 47%;
    }
    @include Tab {
      width: 27%;
    }
    @include DesktopS {
      width: 18%;
      height: 30px;
      width: 20%;
    }
    @include DesktopXL {
      width: 25%;
    }
  }
  /*RATING (STARS) SINGLELOCATION DETAILS PAGE*/
  &__rating {
    height: 18px;
    width: 98px;
    left: 20px;
    top: 489px;

    @include Desktop {
      height: 36px;
      width: 196px;
      left: 1144px;
      top: 652px;
    }
    > svg {
      @include Desktop {
      }
    }
  }
}
/*HOST SINGLELOCATION DETAILS PAGE*/
.host {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 33px;
  width: 40%;
  left: 219px;
  top: 481px;
  @include MobileS {
    width: 44%;
  }
  @include Tab {
    width: 20%;
  }
  @include DesktopS {
    height: 64px;
    width: 14%;
    left: 209px;
    margin-top: 50px;

    margin-bottom: 10px;
  }
  @include Desktop{
    margin-bottom: 0;
  }
  /*NAME HOST SINGLELOCATION DETAILS PAGE*/
  &__name {
    text-align: right;
    height: 33px;
    width: 38%;
    left: 0px;
    top: 0px;
    font-size: 12px;

    @include DesktopS {
      font-size: 14px;
    }
    @include Desktop {
      width: 48%;
      font-size: 18px;
      height: 52px;
      line-height: 26px;
    }
    @include DesktopXL {
      width: 97%;
      font-size: 36px;
      height: 72px;
      line-height: 39px;
    }
  }
  /*IMG HOST (AVATAR) SINGLELOCATION DETAILS PAGE*/
  > img {
    height: 32px;
    width: 32px;
    left: 103.5px;
    top: 0.5px;
    margin-left: 11px;
    border-radius: 50%;
    background: #c4c4c4;
    @include Desktop {
      height: 64px;
      width: 64px;
    }
    @include DesktopXL {
      height: 84px;
      width: 84px;
    }
  }
}
.divRatingHost {
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 10%;
  position: absolute;
  @include MobileS {
    width: 93%;
    margin-top: 12%;
  }
  @include MobileL {
    margin-top: 50px;
  }
  @include Tab {
    width: 90%;
    display: flex;
    position: absolute;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: -7%;
    height: 58px;
  }
  @include DesktopS {
    width: 90%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: -4%;
  }
  @include Desktop {
    width: 86%;
    margin-top: -3%;
    //margin-bottom: 100px;
  }
  @include DesktopXL {
    width: 90%;
    margin-top: -2%;
    //margin-bottom: 100px;
  }
}
.singleLocation-dropdowns {
  display: flex;
  flex-direction: column;
  margin-top: 20%;
  @include MobileS {
    margin-top: 30%;
  }
  @include Tab {
    flex-direction: row;
    width: 100%;
    margin-top: 2%;
    display: flex;
    gap: 20px;
  }
  @include DesktopS {
    flex-direction: row;
    width: 100%;
    margin-top: 2%;
    display: flex;
    gap: 40px;
  }
  @include Desktop {
    margin-top: 5%;
  }
  @include DesktopXL {
    margin-top: 7%;
  }
}
